<template>
  <view class="content">
    <!-- 头部-----------始 -->
    <view class="head">
      <!-- <image src="../../images/bk.jpg"></image> -->
      <text class="head_top">AI林叶害虫智能识别系统</text>
    </view>
    <!-- 头部-----------终 -->
    <!-- 主内容---------始 -->
    <view class="body">
      <view class="content">
        <view class="content0"></view>
        <view class="content1"></view>
        <view class="content2"></view>
        <view class="content3"></view>
        <view class="content4" @click="chooseImage" data-ptype="camera album">
          <u-image shape="circle" width="300rpx" height="300rpx" src="/static/images/bug.jpg"></u-image>
        </view>
      </view>
      <!-- 
        功能项：
        按钮-点击拍照
        链接-点击查找本机相册的照片
       -->
      <u-button @click="chooseImage" :ripple="true" type="primary" ripple-bg-color="#55ffff" data-ptype="camera">点击拍照</u-button>
      <view class="pic">
        <text @click="chooseImage" data-ptype="album">从相册选择 ></text>
      </view>
      
    <!-- 主内容---------终 -->
    </view>
    <view class="u-page">
      <!-- 所有内容的容器 -->
      <!-- 查询结果 -->
      <u-popup v-model="show" mode="bottom" border-radius="14" height="700rpx">
        <view>
          <u-card v-for="petInfo in petsInfo" @click="showPet(petInfo.id)" margin="40rpx 60rpx" :title="petInfo.pastName" :sub-title="petInfo.item + '>' + petInfo.branch + '>' +petInfo.dependant" title-size="40">
            <view class="" slot="body">
              <view class="cardImage">
                <text class="recognition">精度:{{petInfo.accuracy}}</text>
                <u-image width="380rpx" border-radius="25rpx" height="380rpx" :src="petInfo.images.split('#####')[0]"></u-image>
              </view>
              <view style="text-align: center;">
                <text>查看详情 ></text>
              </view>
            </view>
          </u-card>
        </view>
      </u-popup>
    </view>
    
    <!-- 与包裹页面所有内容的元素u-page同级，且在它的下方 -->
    <u-tabbar 
      v-model="current"
      :bg-color="bgColor"
      :border-top="borderTop"
      :list="tabbar"
      :mid-button="midButton"
      :inactive-color="inactiveColor"
      :activeColor="activeColor"
      :change="change"
    ></u-tabbar>
  </view>
</template>

<script>
  export default {
    data() {
      return {
        current: 0,
        bgColor: '#ffffff',
        borderTop: true,
        tabbar: '',
        current: 0,
        midButton: true,
        inactiveColor: '#909399',
        activeColor: '#5098FF',
        show: false,
        imgList: '',
        petsInfo: [],
        petUrl: ''
        // thumb: 'http://pic2.sc.chinaz.com/Files/pic/pic9/202002/hpic2119_s.jpg'
      }
    },
    onLoad() {
      this.tabbar = [{
            "pagePath": "/pages/folkMasters/index",
            "iconPath": "/static/icon/cum.png",
            "selectedIconPath": "/static/icon/cum-full.png",
            "text": "高手社区",
            customIcon: false,
        },
        {
            "pagePath": "/pages/expert/index",
            "iconPath": "/static/icon/rc.png",
            "selectedIconPath": "/static/icon/rc-full.png",
            "text": "专家社区",
            customIcon: false,
        },
        {
            "pagePath": "/pages/index/index",
            "iconPath": "/static/icon/scan.png",
            "selectedIconPath": "/static/icon/scan-full.png",
            "text": "扫一扫",
            "midButton": true,
            "customIcon": false,
        },
        {
            "pagePath": "/pages/petsLibrary/index",
            "iconPath": "/static/icon/bug.png",
            "selectedIconPath": "/static/icon/bug-full.png",
            "text": "智虫库",
            customIcon: false,
        },
        {
            "pagePath": "/pages/user/index",
            "iconPath": "account",
            "selectedIconPath": "account-fill",
            "text": "我",
            customIcon: false,
        }]
    },
    methods: {
      change(index){
        
      },
      chooseImage(e){
        // 判断是从相册获取还是拍照获取
        var type = e.target.dataset.ptype;
        var photoType =  [];
        photoType.push(type);
        
        var self = this;
        
        var imgdata;
        
        uni.chooseImage({
            count: 1, //默认9
            sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
            sourceType: photoType, //从相册选择
            
            success: function (res) {
              // 图片本地地址
              // console.log(JSON.stringify(res.tempFilePaths));
              
              self.urlToBase64(res.tempFilePaths[0]).then(res=>{
                // Base64图片信息
                self.imgList = self.imgList.concat(res);
                self.getPetInfo()
              });
              
              // console.log(self.imgList);
              // 传递照片获取信息
            },
            
            
        });
      },
      // 函数:显示pet信息界面
      showPet(petId){
        uni.navigateTo({
            url:"/pages/petsLibrary/pet?id=" + petId,
            // url:"../component/classdetails/classdetails?newsid="+ newsid,
            // success:res =>{},
            // fail:() =>{},
            // complete:() => {}
        });
      },
      // 函数:将图片转换为base64
      urlToBase64(url){
        return new Promise((resolve, reject)=>{
          uni.request({
            url: url,
            method:'GET',
            responseType: 'arraybuffer',
            success: res => {
              let base64 = wx.arrayBufferToBase64(res.data); //把arraybuffer转成base64
              base64 = 'data:image/jpeg;base64,' + base64 //不加上这串字符，在页面无法显示的
              // console.log(base64)
              resolve(base64)
            },
            fail: err=>{
              reject(err)
            }
          });
        })
      },
      // 函数:进行上传照片和分析结果请求
      getPetInfo(){
        var imgdata = {
          "oneImg": this.imgList
        }
        
        uni.request({
          url: 'http://ocr.server.truedei.com/ocr/ocrOnePic',
          method:'POST',
          data: imgdata,
          success: res => {
            console.log(res);
            // 保存查询后的害虫数据
            this.petsInfo = res.data.data;
            // this.petUrl = res.data.url;
            // 显示隐藏框
            this.show = true;
            console.log(this.petsInfo);
          },
          fail: err=>{
            reject(err);
          }
        });
      }      
    }
  }
</script>

<style lang="scss" scoped>
  .content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 40rpx;
  }

  .logo {
    height: 200rpx;
    width: 200rpx;
    margin-top: 100rpx;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50rpx;
  }

  .text-area {
    display: flex;
    justify-content: center;
  }
  
  .title {
    font-size: 28rpx;
    color: $u-content-color;
  }
  
  .button-demo {
    margin-top: 80rpx;
  }
  
  .link-demo {
    margin-top: 80rpx;
  }
  
  // 卡片样式
  view.cardImage{
    display: inline-block;
    position: relative;
    left: 50%;
    margin-left: -190rpx;
    width: 380rpx;
    height: 380rpx;
  }
  
  /* 初始化样式 */
  view, image {
    margin: 0;
    padding: 0;
  }
  
  /* 头部样式-----------------始 */
  .head { 
    position: relative;
    width: 100%;
    height: 240rpx;
  }
  /* 头部背景 */
  .head image {
    width: 100%;
    height: 240rpx;
  }
  /* 头部标题 */
  .head .head_top {
    position: absolute;
    top: 20%;
    left: 50%;
    margin-left:-5em;
    font-weight: 700;
    font-size: 20px;
  }
  /* 头部样式----------------------终 */
  
  
  /* 主内容样式--------------------始 */
  .body {
    width: 750rpx;
    height: 700rpx;
  }
  .body .content {
    position: relative;
    height: 400rpx;
    display: grid;
    place-items: center;
  }
  .body .content0,
  .body .content1,
  .body .content2,
  .body .content3 {
    position: absolute;
  }
  
  .body .content0 {
  
    z-index: 555;
    left: 50%;
    margin-left: -230rpx;
    width: 460rpx;
    height: 460rpx;
    background-color: rgba(88, 181, 230, 0.1);
    border-radius: 220rpx;
    -webkit-animation: fade_0 1.7s ease-in 0s infinite;
    animation: fade_0 1.7s ease-in 0s infinite;
  }
  .body .content1 {
    position: absolute;
    z-index: 666;
    left: 50%;
    margin-left: -205rpx;
    width: 410rpx;
    height: 410rpx;
    background-color: rgba(88, 181, 230, 0.2);
    border-radius: 200rpx;
    -webkit-animation: fade_3 1.7s ease-in 0s infinite;
    animation: fade_3 1.7s ease-in 0s infinite;
  }
  .body .content2 {
    /*水平垂直居中*/
    position: absolute;
    z-index: 777;
    left: 50%;
    margin-left: -180rpx;
    width: 360rpx;
    height: 360rpx;
    background-color: rgba(88, 181, 230, 0.5);
    border-radius: 180rpx;
    -webkit-animation: fade_2 1.7s ease-in 0s infinite;
    animation: fade_2 1.7s ease-in 0s infinite;
  }
  .body .content3 {
    position: absolute;
    z-index: 888;
    /*水平垂直居中*/
    left: 50%;
    margin-left: -150rpx;
    width: 300rpx;
    height: 300rpx;
    background-color: rgba(88, 181, 230, 1.0);
    border-radius: 150rpx;
    -webkit-animation: fade_1 1.7s ease-in 0s infinite;
    animation: fade_1 1.7s ease-in 0s infinite;
  }
  .body .content4 {
    position: absolute;
    left: 50%;
    margin-left: -150rpx;
    z-index: 999;
    display: inline-block;
    width: 300rpx;
    height: 300rpx;
    border-radius: 150rpx;
    -webkit-animation: fade_5 1.7s ease-in 0s infinite;
    animation: fade_5 1.7s ease-in 0s infinite;
  }
  
  /* 拍照按钮 */
  .body button {
    width: 280rpx;
    margin-top: 200rpx;
  }
  
  /* 从相册选择照片*/
  .body .pic {
    margin-top: 40rpx;
    text-align: center;
  }
  /* 主内容样式--------------------终 */
  
  .recognition {
    display: inline-block;
    margin-bottom: .5em;
    width: 100%;
    text-align: center;
    color: green;
  }
  
  /* 动画 */
  @-webkit-keyframes fade_1 {
    0% {opacity: 0;}
    12% { opacity: 1;}
    24% {opacity: 1;}
    36% {opacity: 1;}
    48% {opacity: 1;}
    60% {opacity: 0;}
    72% { opacity: 0;}
    88% { opacity: 0;}
    100% { opacity: 0;}
  }
  
  @-webkit-keyframes fade_2 {
    0% {opacity: 0;}
    12% { opacity: 0;}
    24% {opacity: 1;}
    36% {opacity: 1;}
    48% {opacity: 1;}
    60% {opacity: 1;}
    72% { opacity: 0;}
    88% { opacity: 0;}
    100% { opacity: 0;}
  }
  
  @-webkit-keyframes fade_3 {
    0% {opacity: 0;}
    12% { opacity: 0;}
    24% {opacity: 0;}
    36% {opacity: 1;}
    48% {opacity: 1;}
    60% {opacity: 1;}
    72% { opacity: 1;}
    88% { opacity: 0;}
    100% { opacity: 0;}
  }
  
  @-webkit-keyframes fade_0 {
    0% {opacity: 0;}
    12% { opacity: 0;}
    24% {opacity: 0;}
    36% {opacity: 0;}
    48% {opacity: 1;}
    60% {opacity: 1;}
    72% { opacity: 1;}
    88% { opacity: 1;}
    100% { opacity: 0;}
  }
</style>
